.modal{
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0%;
    left: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 12;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}
.modal.active {
    opacity: 1;
    pointer-events: all;

}
.modal__content {
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    border-radius: 15px;

}
.modal__content.active{
    transform: scale(1);
}